<template>
  <el-container>
    <!-- 侧边开始 -->
    <!-- 是否使用 vue-router 的模式，启用该模式会在激活导航时以 index 作为 path 进行路由跳转 -->
  <el-aside width="200px">
    <el-menu :data="user"
    class="el-menu-vertical-demo"
      background-color="#545c64"
      text-color="#fff"
      router
      active-text-color="#ffd04b" :unique-opened="true">
      <el-menu-item index="/home">
        <i class="el-icon-menu"></i>
        <span slot="title">首页</span>
      </el-menu-item>
        <template v-for="(item,index) in user.menus">
            <!-- 如果有children,显示目录,遍历菜单 -->
           <el-submenu v-if="item.children" :key="index" :index="item.title">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span>{{item.title}}</span>
            </template>
            <template>
              <el-menu-item v-for="(i,m) in item.children" :key="m" :index="i.url">{{i.title}}</el-menu-item>
            </template>
          </el-submenu>
          <!-- 如果有没有children,直接显示菜单 -->
          <el-menu-item  v-if="!item.children" :key="item.id" :index="item.url">{{item.title}}</el-menu-item>

        </template>




      </el-menu>
  </el-aside>
  <!-- 侧边结束 -->
  <el-container>
    <el-header>
      <div class="header-con">
        <span>{{user.username}}</span>
        <el-button type="primary" @click="logout">退出</el-button>
      </div>
    </el-header>
    <el-main>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>{{$route.name}}</el-breadcrumb-item>
      </el-breadcrumb>
      <router-view></router-view>
    </el-main>
  </el-container>
</el-container>
</template>

<script>
import {mapGetters,mapActions} from 'vuex';
export default {

  computed:{
    ...mapGetters({
      user:'user',
    }),
    hasChildren(){
      return this.user.menus[0].children ? true : false
    }

  },
  methods:{
    ...mapActions({
      clearUser:"requestLoginActions"
    }),
    logout(){
      this.clearUser(null);
      this.$router.push('login');
    }
  },
}
</script>

<style scoped>
.el-aside{
  height: 100vh;
  background:black;
  color:white;
}
.el-header{
  background:blue;
}
.header-con{
  float:right;
}
.header-con span{
  line-height: 60px;
  color:white;
}
.el-breadcrumb{
  padding-bottom:15px;
}
</style>



